  width: 100%;
}
.projectWrap .projectTitleWrap .projectTitle {
  font-size: var(--font-size-lg-mx);
}
.projectWrap .projectTitleWrap .projectSubTitle {
  font-size: var(--font-size-md-mx);
  opacity: 0.7;
}
.projectWrap .projectCaptureWrap {
  overflow: hidden;
  height: 50%;
  width: 100%;
}
.projectWrap .projectCaptureWrap .projectCapture {
  height: calc(100% - var(--font-size-lg-mx));
  width: 100%;
  padding: 1vw;
}
.projectWrap .projectCaptureWrap .projectCapture img {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
.projectWrap .projectCaptureWrap .moveBtnDiv {
  position: relative;
  height: var(--font-size-lg-mx);
  font-size: var(--font-size-md-mx);
}
.projectWrap .projectCaptureWrap .moveBtnDiv .notionBtn {
  position: absolute;
  left: 8px;
}
.projectWrap .projectCaptureWrap .moveBtnDiv .notionBtn img {
  /* height: 100%; */
  height: var(--font-size-lg-mx);
}
.projectWrap .projectCaptureWrap .moveBtnDiv .v-icon {
  font-size: var(--font-size-lg);
}
.projectWrap .projectDetailWrap {
  width: 100%;
  height: calc(50% - var(--font-size-lg-mx) - var(--font-size-md-mx));
  /* background: rgb(var(--v-theme-secondary)); */
  font-size: var(--font-size-sm-mx);
}
</style>
