<template>
  <div
    class="timelineWrap d-flex flex-column align-center text-font font-weight-bold"
  >
    <div class="timelineTop d-flex justify-space-between">
      <v-card
        class="d-flex justify-center align-center bg-secondary-lighten-1"
        color="primaryBold"
        variant="tonal"
        >#Timeline</v-card
      >
      <v-btn
        class="blinking font-weight-bold"
        variant="outlined"
        @click="movePage"
        height="100%"
        color="primaryBold"
        >{{ this.page === 0 ? 'next' : 'prev' }}</v-btn
      >
    </div>

    <div
      class="timeline d-flex flex-row align-center justify-center py-3 elevation-2 bg-white"
      v-for="(item, i) in timelines"
      :key="i"
    >
      <div class="timelineDate d-flex flex-column align-end">
        <span v-for="(period, j) in item.date.split('~')" :key="j">{{
          period
        }}</span>
      </div>
      <v-btn icon color="primary"
        ><v-icon color="white" :icon="item.icon"></v-icon
      ></v-btn>
      <div class="timelineDetail d-flex flex-column justify-center px-2">
        <span class="timelineTitle">{{ item.title }}</span>
        <v-divider :thickness="2"></v-divider>
        <span class="timelineText">{{ item.value }}</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      page: 0,
      timelines: [],
      timelineList: [
        {
          date: '2013.03',
          title: '인하공업전문대학교 입학',
          value: '컴퓨터정보과 13학번',
          icon: 'mdi-book-open-blank-variant'
        },
        {
          date: '2013.08',
          title: 'IT BANK 수강',
          value: '네트워크/보안 과정 수료',
          icon: 'mdi-book-open-blank-variant'
        },
        {
          date: '2015.08~2017.05',
          title: '군입대',
          value: '육군병장 만기전역',
          icon: 'mdi-star-check'
        },
        {
          date: '2018.07',
          title: '대외활동',
          value: 'K-헤커톤/제주 참가',
          icon: 'mdi-run'
        },
        {
          date: '2018.09',
          title: '이나우테크놀로지 입사',
          value: '플랫폼개발2팀/SK TANGO Project',
          icon: 'mdi-laptop'
        },
        {
          date: '2019.02',
          title: '인하공업전문대학교 졸업',
          value: '졸업',
          icon: 'mdi-school'
        },
        {
          date: '2019.03',
          title: '이나우테크놀로지 퇴사',
          value: '경영악화',
          icon: 'mdi-laptop-off'
        },
        {
          date: '2019.05~2021.05',
          title: '아름넷닷컴',
          value: '기술연구소 / Vue.js',
          icon: 'mdi-laptop'
        },
        {
          date: '2021.08',
          title: '자격증 취득',
          value: '정보처리기사',
          icon: 'mdi-card-account-details-outline'
        },
        {
          date: '2021.10~2022.11',
          title: '코비젼',
          value: '클라우드 사업본부 하랑사업팀',
          icon: 'mdi-laptop'
        }
      ]
    }
  },
  mounted() {
    this.getCurPage()
  },
  methods: {
    getCurPage() {
      this.timelines = this.timelineList.filter((item, i) => {
        return Math.floor(i / 5) === this.page
      })
    },
    movePage() {
      this.page === 0 ? (this.page = 1) : (this.page = 0)
      this.getCurPage()
    }
  }
}
</script>
<style>
.timelineWrap {
  width: 100%;
}

.timelineWrap .timelineTop {
  width: 95%;
}

.timelineWrap .timelineTop .v-card {
  border: 2px solid rgb(var(--v-theme-secondary));
  padding-left: 1vw;
  padding-right: 1vw;
  font-size: var(--font-size-lg-mx);
}
.timelineWrap .timelineTop .v-btn {
  font-size: var(--font-size-lg-mx);
  border: 1px solid rgb(var(--v-theme-primaryBold));
}
.timelineWrap .timeline {
  width: 95%;
  margin-top: 0.5vh;
  margin-bottom: 0.5vh;
}

.timelineWrap .timeline .timelineDate {
  width: 20%;
  font-size: var(--font-size-sm-mx);
  padding-right: 4px;
}
.timelineWrap .timeline .v-btn {
  width: max(4vw, 35px);
  height: max(4vw, 35px);
  margin-right: 4px;
}
.timelineWrap .timeline .timelineDetail {
  width: calc(80% - max(4vw, 35px) - 8px);
}
.timelineWrap .timeline .timelineDetail .timelineTitle {
  font-size: var(--font-size-md-mx);
}
.timelineWrap .timeline .timelineDetail .timelineText {
  font-size: var(--font-size-sm-mx);
}
</style>
